	module.exports = {"data":{"site":{"siteMetadata":{"title":"The Personal Website of Brandon Brown","author":"Brandon Brown"}},"markdownRemark":{"id":"/Users/temp/Workspace/brandonbrown.github.io/src/pages/writing/web-people/index.md absPath of file >>> MarkdownRemark","html":"<p>The web industry has a reputation for attracting, and sometimes cultivating, people who would find themselves at the fringes of the normal world. Now I do admit that as the industry has grown and matured, it’s difficult not to bump into another pixel-worker in day-to-day life. Somehow, 90% of the people I’ve met in this industry have been incredible in some form.</p>\n<p>There’s a palatable sense of friendliness, acceptance, motivation, and care that seems to be carried by most people in this industry. This feeling is one of my favorite parts of calling myself a web-worker. Seriously, most people in web rock.</p>\n<p>I’ve started gathering a list of people I highly recommend paying attention to, getting to know, and to appreciate what they’ve done for all of their peers in the web industry. By no means is this the end-all be-all of web lists.</p>\n<p>This list is going to include names such as:</p>\n<ul>\n<li>Chris Coyier</li>\n<li>Sarah Parmenter</li>\n<li>Ethan Marcotte</li>\n<li>Denise Jacobs</li>\n<li>Wes Bos</li>\n<li>NPJ</li>\n<li>Jeffrey Zeldman</li>\n<li>Chris Do</li>\n<li>David Walsh</li>\n<li>Eric Meyer</li>\n<li>Rachel Andrew</li>\n<li>Jen Simmons</li>\n<li>Brad Frost</li>\n<li><strong>and so many more</strong></li>\n</ul>\n<p>Seriously folks, you’re all incredible. Thank you for everything you’ve done for our industry and all of those people you’ve helped grow to be the next generation of pixel-workers.</p>","frontmatter":{"title":"Web People Rock","date":"July 22, 2019","category":"Resources"}}},"pathContext":{"slug":"/writing/web-people/","category":"Resources","previous":{"fields":{"slug":"/writing/the-fast-app/"},"frontmatter":{"title":"The Fast App","category":"Development","posttype":"writing"}},"next":{"fields":{"slug":"/bkmks/bookmarks-1/"},"frontmatter":{"title":"BKMKS1 Oct 6 2019","category":"Resources","posttype":"bkmk"}}}}

